body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
}
 
.game {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  /* max-width: 600px;  设置一个最大宽度，防止在大屏幕上过大  */
  margin: auto;
  text-align: center;
}
 
.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px; /* 给游戏信息区域留出一些空间 */
}
 
.board-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 10px 0;
}
 
.square {
  width: 40px; /* 增大格子宽度 */
  height: 40px; /* 增大格子高度 */
  line-height: 40px; /* 使文字垂直居中 */
  font-size: 20px; /* 增大文字大小 */
  margin: 2px; /* 格子之间的间距 */
  background-color: #f0d9b5; /* 浅黄色背景 */
  border: 1px solid #ccc; /* 灰色边框 */
  cursor: pointer; /* 鼠标悬停时显示为可点击 */
}
 
.square:hover {
  background-color: #ffe4b5; /* 鼠标悬停时背景颜色变深 */
}
 
.status {
  margin-bottom: 10px; /* 给棋盘留出一些空间 */
  font-size: 18px; /* 状态文字大小 */
}
 
.game-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 游戏信息区域左对齐 */
  width: 100%;
  margin-left: 20px;
}
 
.game-info ol {
  list-style-type: none; /* 去掉列表项的默认样式 */
  padding: 0; /* 去掉内边距 */
}
 
.game-info li {
  margin: 5px 0; /* 列表项之间的间距 */
}
 
.game-info button {
  padding: 5px 10px; /* 按钮内边距 */
  font-size: 16px; /* 按钮文字大小 */
  cursor: pointer; /* 鼠标悬停时显示为可点击 */
}


